* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100%;
}

.top {
  justify-content: space-between;
}

header {
  background-color: rgb(221, 215, 215);
  padding: 5px 20px;
  /* height: 60px; */
}

header a {
  padding: 3px;
}

header a:hover {
  font-weight: 700;
  padding: 3px 0px;
  border-bottom: 2px solid black;
  padding-bottom: 1px;
  border-radius: 7px;
}

header svg {
  margin: 4px;
  font-size: 22px;
}

header p {
  margin: 1px;
}

.navbar{
  position: sticky;
  top: 0px;
  z-index: 20;
}

.logo {
  width: 120px;
  height: 45px;
}

.social {
  margin: 0px 10px;
}

.social a {
  margin: 0px 6px;
}

.social a:hover {
  border: 1.5px solid black;
  border-radius: 30px;
  padding-left: 1px;
  padding-right: 1px;
  background-color: #f5f5f5;
}

.nav-item {
  margin: 8px 12px;
}

.nav-link:hover {
  /* text-decoration: underline; */
  color: burlywood !important;
  /* line-height: 25px; */
  /* letter-spacing: 1px; */
  font-family: ubuntu;
  font-weight: 600;
  border-bottom: 2px solid white;
}

.navbar-nav {
  position: absolute;
  z-index: 10;
  background-color: black;
  width: -webkit-fill-available;
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-dark-rgb),var(--bs-bg-opacity))!important;
}

.C-img {
  height: 450px;
}

.mainh2 {
  font-family: sans-serif;
  font-size: 25px;
  color: #644141;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 6px;
  letter-spacing: 0.1px;
}

.mainp {
  font-family: auto;
  font-size: 18px;
  font-weight: 500;
  width: 86vw;
  margin: 5px auto;
  text-align: center;
}

.mainCards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 21rem;
  height: 90%;
  margin: 20px 12px;
  padding: 10px;
  text-align: center;
}

.card:hover {
  cursor: pointer;
  background: transparent !important;
  border: 3px solid grey;
  box-sizing: border-box;
  box-shadow: -1px 5px 12px 4px #6a705f;
}

.card-btn:hover {
  background: transparent;
  border: 1.5px solid rgb(107, 101, 101);
  color: black;
  font-weight: 600;
}

.card img {
  margin: 14px 16px;
}

.card-body h5 {
  font-family: monospace;
  line-height: 24px;
  letter-spacing: 0.3px;
  font-weight: 600;
}

.card p {
  font-family: "Ubuntu", sans-serif;
  font-size: 15px;
  width: 90%;
  margin: 20px auto;
}

.numbers {
  margin: 0px 0vw;
  padding: 15px 12vw;
  justify-content: space-between;
  filter: saturate(5);
  filter: brightness(100%);
  filter: contrast(100%);
}

.number-item {
  width: 25%;
  text-align: center;
}

.number-item h4 {
  font-size: 24px;
  font-family: cursive;
  font-weight: bolder;
  margin-bottom: 0px;
  text-align: center;
}

.number-item p {
  font-size: 18px;
  font-weight: 600;
}

.partner {
  margin: 7vw 10vw;
  padding: 50px 0px;
  border-top: 2px solid gray;
  border-bottom: 2px solid gray;
  overflow-x: hidden;
}

.partner h3 {
  text-align: center;
  font-weight: bolder;
  color: #6a705f;
  margin-bottom: 50px;
  font-family: ubuntu;
}

.slider {
  display: flex;
  position: relative;
  overflow-x: visible;
  animation: scroll 25s linear infinite;
  height: 100%;
  width: 80vw;
}

@keyframes scroll {
  from {
    left: 50px;
  }
  to {
    left: -2590px;
  }
}

.slider::-webkit-scrollbar {
  display: none;
}

.partner-slide {
  width: 20vw;
  height: 100%;
  text-align: center;
  width: 250px;
}

.slide-image {
  margin: 0px 5px;
  height: 100px;
  object-fit: contain;
  width: 200px;
}

.contact form {
  width: 80%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: fit-content;
  background-color: #8eb9f6;
  padding: 20px 5vw;
  border-radius: 15px;
  margin: auto;
}

.contact h4 {
  font-size: 25px;
  font-family: ubuntu;
  font-weight: 500;
  text-align: center;
  color: black;
  margin: 30px 0px;
}

.contact input {
  width: 28vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

.contact textarea {
  width: 63vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

.contact-btn:hover {
  background-color: green !important;
  color: purple !important;
}

.footer {
  background-color: rgb(46, 41, 41);
  color: white;
  margin-top: 80px;
  margin-bottom: 0px;
  padding: 50px 10px;
  padding-bottom: 3px;
}

footer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 20px 13px;
}

.footer-section {
  width: 22%;
  margin: 25px 10px;
}
.footer-section h5 {
  font-family: ubuntu;
  font-weight: 800;
}
.footer-section a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.footer-section a:hover {
  color: #9c9e98;
}

.last {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  margin: 40px 0px;
  padding: 0px 13vw;
  color: white;
}

.last svg {
  background-color: rgb(255, 255, 255);
  font-size: 30px;
  border-radius: 8px;
}

.Costumer-support {
  /* width: 40%; */
  text-align: center;
}

.testimonial{
  width: 100%;
}

.testimonial{
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  margin: 20px auto;
}

.testimonials h3 {
  text-align: center;
  font-weight: bolder;
  color: #6a705f;
  margin: 25px 0px;
  font-family: ubuntu;
}

.testimonial-card-body{
  display: none;
}

.card{
  border: none;
}

.card h5{
  font-size: 21px;
  font-weight: 600;
  font-family: ubuntu;
}

.card-img-top{
  width: 70%;
  height: 150px;
  margin: 20px auto!important;
}

.text-slider{
  width: 91%;
  margin: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-style: oblique;
}

.main{
  background-color: #7a7e75;
  color: white;
  border-radius: 8px;
  text-align: center;
  padding-bottom: 40px;
}

.main h3{
  padding: 35px 0px;
}

.main p{
  text-align: left;
  padding: 30px 7%;
}

.btns{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: -40px 10px;
}

.btns button{
  width: fit-content;
  border-radius: 30px;
  border: 2px solid black;
  padding: 10px 25px;
  margin: 20px;
  margin-bottom: 30px;
  /* margin-top: -10px; */
}

.btn-content{
  margin: 70px auto;
  width: 82vw;
}

#c2,#c3,#c4,#c5,#c6{
  display: none;
}

.testimony{
  width: 100%;
  margin: 30px auto;
  flex-wrap: wrap;
  justify-content: center;
}

/*.card:hover .card-body{
  display: block; 
}*/

@media only screen and (min-width: 0px) and (max-width: 750px) {
  .top p {
    display: none;
  }
  header {
    padding: 0;
  }
  .social {
    margin-right: 3px;
  }
  header svg {
    font-size: 17px;
  }
  .logo {
    margin-left: 0px !important;
    /* height: 38px; */
  }

  .footer-section{
    width: 42vw;
  }
}

@media only screen and (min-width: 0px) and (max-width: 1150px) {
  /* .nav-item a{
    padding-left: 0px;
    padding-right: 0px;
    margin-left: 5px;
    margin-right: 5px;
  } */

  .navbar {
    height: fit-content !important;
  }

  .nav-item {
    margin: 8px 6px;
  }

  .signup{
    width: 80%!important;
  }
}

@media only screen and (min-width: 0px) and (max-width: 600px) {
  .C-img {
    height: 250px;
  }
  .mainh2 {
    font-size: 20px;
    width: 90vw;
    margin: auto;
    margin-top: 20px;
  }
  .mainp {
    font-size: 14px;
  }
  .partner {
    margin: 7vw 4vw;
    padding: 10px 0px;
    padding-top: 20px;
  }
  .slide-image {
    height: 50px;
    width: 100px;
  }

  .slider {
    display: flex;
    position: relative;
    overflow-x: visible;
    animation: scroll 13s linear infinite;
    height: 100%;
    width: 80vw;
  }

  @keyframes scroll {
    from {
      left: 15px;
    }
    to {
      left: -1535px;
    }
  }

  .numbers {
    width: 100%;
    padding: 20px !important;
    margin: 20px auto !important;
  }

  .number-item {
    width: 30%;
  }

  .number-item h4 {
    font-size: 18px;
  }

  .number-item p {
    font-size: 14px;
  }

  .card{
    width: 18rem;
  }

  .contact form {
    width: 94%;
  }

  .contact h4 {
    font-size: 20px;
  }

  .contact input {
    width: 100%;
  }

  .contact textarea {
    width: 100%;
  }

  .contact-btn{
    width: 100%!important;
  }

  .footer{
    text-align: center;
  }

  .footer-section{
    text-align: left;
    width: fit-content;
  }

  .last{
    /* text-align: left; */
    justify-content: flex-start;
  }

  .Costumer-support{
    margin-bottom: 50px!important;
    margin-left: 7vw;
  }
}
